import { Component, Prop, h, Event, EventEmitter, State } from "@stencil/core";





@Component({
    tag: 'jji-guest-details',
    styleUrl: './guest-details.css',
})
export class GuestDetails {



    //Controls the visibility of the sign-up form.
    @Prop({ reflect: true, mutable: true }) opened: boolean;


    @Event({ bubbles: true, composed: true }) formDataSubmitted: EventEmitter<any>;

    @State() firstname: string = '';
    @State() lastname: string = '';
    @State() email: string = '';
    @State() alternateemail: string = '';
    @State() password: string = '';
    @State() country: string = '';
    @State() city: string = '';
    @State() adress: string = '';
    @State() countrycode: string = '';
    @State() mobilenumber: string = '';
    @State() newsletter: boolean = false;

    //Indicates whether the form has been submitted.
    @State() formSubmitted: boolean = false;

    //New state variable to control the error message
    @State() showFormError: boolean = false;


    handleCheckboxChange(event: Event) {
        const checkbox = event.target as HTMLInputElement;
        this.newsletter = checkbox.checked;
    }

    async handleSubmit(event: Event) {
        event.preventDefault();
        const formData = {
            firstname: this.firstname,
            lastname: this.lastname,
            email: this.email,
            alternateemail: this.alternateemail,
            password: this.password,
            country: this.country,
            city: this.city,
            adress: this.adress,
            countrycode: this.countrycode,
            mobilenumber: this.mobilenumber,
            newsletter: this.newsletter,
        };
        //Emit the form data through the custom event
        this.formDataSubmitted.emit(formData);
        console.log(formData);
    }

    saveGuestDetailsClick(event: MouseEvent) {
        // Prevent the default form submission behavior
        event.preventDefault();

        // Check if any required fields are empty
        if (this.firstname === '' || this.lastname === '' || this.email === '' || this.password === '' || this.country === '' || this.countrycode === '' || this.mobilenumber === '') {
            // Show the error message when the "Save" button is clicked and required fields are empty
            this.showFormError = true;
            console.log('Error!')
        } else {
            // If all required fields are filled, perform form submission
            this.handleSubmit(event);
            console.log(event);

        }
    }

    render() {
        let mainContent = null;

        if (this.opened) {
            mainContent = [
                <div id="main-container">
                    <h3>Guest Details</h3>
                    <div id="form-container">
                        <div>
                            <form action="">
                                <div>
                                    <p id="rg-date">Registration date : <span id="a">27-jul-2023</span></p>
                                </div>
                                <div class="input-container">
                                    <label htmlFor="firstName">First Name*</label>
                                    <input
                                        type="text"
                                        id="firstName"
                                        placeholder=""
                                        value={this.firstname}
                                        onChange={(event) => this.firstname = (event.target as HTMLInputElement).value} />
                                </div>
                                <div class="input-container">
                                    <label htmlFor="lastname">Last Name*</label>
                                    <input
                                        type="text"
                                        id="lastName"
                                        placeholder=""
                                        value={this.lastname}
                                        onChange={(event) => this.lastname = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div class="input-container">
                                    <label htmlFor="email">Email*</label>
                                    <input
                                        type="email"
                                        id="email"
                                        placeholder=""
                                        value={this.email}
                                        onChange={(event) => this.email = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div class="input-container">
                                    <label htmlFor="email">Alternate email</label>
                                    <input
                                        type="email"
                                        id="Alt-email"
                                        placeholder=""
                                        value={this.alternateemail}
                                        onChange={(event) => this.alternateemail = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div class="input-container">
                                    <label htmlFor="password">Password*</label>
                                    <input
                                        type="password"
                                        id="password"
                                        placeholder=""
                                        value={this.password}
                                        onChange={(event) => this.password = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div class="input-container">
                                    <label htmlFor="country">Country*</label>
                                    <select
                                        class="country-dropdown"
                                        id="country"
                                        onChange={(event) => this.country = (event.target as HTMLSelectElement).value}
                                    >
                                        <option value="" disabled selected>Select Your Country</option>
                                        <option value="USA">USA</option>
                                        <option value="Canada">Canada</option>
                                    </select>
                                </div>
                                <div class="input-container">
                                    <label htmlFor="city">City</label>
                                    <input
                                        type="text"
                                        id="city"
                                        placeholder=""
                                        value={this.city}
                                        onChange={(event) => this.city = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div class="input-container">
                                    <label id="address-lable" htmlFor="address">Address</label>
                                    <textarea
                                        class=""
                                        id="address-textarea"
                                        rows={2}
                                        placeholder=""
                                        value={this.adress}
                                        onInput={(event) => (this.adress = (event.target as HTMLTextAreaElement).value)}
                                    ></textarea>
                                </div>
                                <div class="input-container">
                                    <label htmlFor="countryCode">Mobile</label>
                                    <select
                                        id="countryCode"
                                        onChange={(event) => (this.countrycode = (event.target as HTMLSelectElement).value)}
                                    >
                                        <option value="" disabled selected>Code</option>
                                        <option value="+1">+1 (USA)</option>
                                        <option value="+44">+44 (UK)</option>
                                    </select>
                                    <br />
                                </div>
                                <div class="">
                                    <input
                                        type="tel"
                                        id="mobileNumber"
                                        placeholder=""
                                        value={this.mobilenumber}
                                        onChange={(event) => this.mobilenumber = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div class="checkbox">
                                    <input
                                        type="checkbox"
                                        id="termsCheckbox"
                                        name="termsCheckbox"
                                        onChange={(event) => this.handleCheckboxChange(event)}
                                    />
                                    <label htmlFor="termsCheckbox">Newsletter</label>
                                </div>
                                <div>
                                    <span>Last used :</span>
                                    <span>Language: <span id="b">EN</span></span>
                                    <span>--- Currency: <span id="c">â‚¬</span></span>
                                </div>

                                <div class="line-separator"></div>

                                <button
                                    type="submit"
                                    class="full-width-button"
                                    onClick={(event) => this.saveGuestDetailsClick(event)}
                                >
                                    Save
                                </button>
                            </form>
                        </div>
                    </div>
                </div>
            ]
        }
        return (
            <div>
                {mainContent}
            </div>

        )
    }
}